import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import {get_api} from "../env";

Vue.use(VueAxios, axios);

export const baseApi = get_api(process.env.NODE_ENV);

const request = axios.create({
    baseURL: baseApi.config.api,
    timeout: 25000,
});

// 
request.interceptors.request.use(function (config) {
    //  api 
    if (config.url.indexOf('http') !== 0) {
        config.emulateJSON = true;
        config.withCredentials = true;
        config.headers['X-TOKEN'] = localStorage.getItem('x-token');
        config.headers['Content-Type'] = 'application/json';
    }

    return config;
}, function (error) {
    console.error(error);
    return Promise.reject(error);
});

// 
request.interceptors.response.use(function (response) {
    //   
    if (+response.data.code === 10003) {
        localStorage.removeItem('x-token');
        location.reload();
    }

    // code0
    if (+response.data.code !== 0) {
        Promise.reject(response.data.message);
    }
    if (!response.data.data) {
        response.data.data = [];
    }
    return response;
}, function (error) {
    console.error(error);
    Vue.prototype.$notify(
        {
            title: '',
            message: '',
            type: 'error',
            duration: 3000,
        }
    );
    return Promise.reject('');
});

//  API 
export const createAPI = (url, method, config = {}) => {
    return request({
        url,
        method,
        ...config
    });
};